@media only screen and (max-width: 767px) {
  /* Mobile-specific styles go here */
  .cell {
    width: 30px;
    height: 30px;
    border-right: 1px #666 solid;
    border-bottom: 1px #666 solid;
    border-top: 1px #666 solid;
    border-left: 1px #666 solid;
    background-color: #efefef;
    line-height: 25px;
    font-family: "Roboto", sans-serif;
    font-size: 12px;
    transition: all 0.1s;
    user-select: none;
    color: #555;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .centeralignment {
    display: flex;
    justify-content: left;
  }
}

/* Styles for screens with a minimum width of 768px and a maximum of 1023px (Tablet) */

@media only screen and (min-width: 768px) and (max-width: 1023px) {
  /* Tablet-specific styles go here */
  .cell {
    width: 32px;
    height: 32px;
    border-right: 1px #666 solid;
    border-bottom: 1px #666 solid;
    border-top: 1px #666 solid;
    border-left: 1px #666 solid;
    background-color: #efefef;
    line-height: 25px;
    font-family: "Roboto", sans-serif;
    font-size: 12px;
    transition: all 0.1s;
    user-select: none;
    color: #555;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .centeralignment {
    display: flex;
    justify-content: center;
  }
}

/* Styles for screens with a minimum width of 1024px (Desktop) */

@media only screen and (min-width: 1024px) {
  /* Desktop-specific styles go here */

  .cell {
    width: 35px;
    height: 35px;
    border-right: 1px #666 solid;
    border-bottom: 1px #666 solid;
    border-top: 1px #666 solid;
    border-left: 1px #666 solid;
    background-color: #efefef;
    line-height: 25px;
    font-family: "Roboto", sans-serif;
    font-size: 12px;
    transition: all 0.1s;
    user-select: none;
    color: #555;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .centeralignment {
    display: flex;
    justify-content: center;
  }
}

