.cell:hover {
  background-color: #11a7e1;
  color: #fcfcfc;
  cursor: pointer;
}

.cell.hilighted {
  background-color: #11a7e1;
  color: #fcfcfc;
}

.grid {
  margin: 0%;
  padding: 0%;
}

#word-list {
  border: 1px #ececec solid;
  letter-spacing: 1px;
  background-color: #ddd;
  height: max-content;
}

#word-list li {
  padding: 5px 10px;
  margin: 5px;
  border-left: 1px #333 solid;
  transition: all 0.3s;
  font-weight: lighter;
  background-color: #f0f0f0;
}

#word-list li:hover {
  background-color: #e0e0e0;
  cursor: pointer;
}

.ws-my-page-success-toast {
  width: auto;
  background-color: #7c6;
  position: relative;
  margin: auto;
  text-align: center;
  padding: 5px;
  color: #fff;
}

#timer {
  background-color: #fff;
  width: 80%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #202020;
  border-radius: 5px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.3);
  letter-spacing: 3px;
  font-weight: bolder;
  font-size: 2rem;
  margin-top: 2%;
}

.mainheading {
  font-size: calc(1rem+1.6vw);
  font-weight: bolder;
}

.subheading {
  font-size: calc(0.8rem+1.2vw);
  font-weight: bolder;
}

.content {
  font-size: calc(0.6rem+1vw);
}
